<template>
  <div>
    <h1></h1>
    <el-table :data="items">
      <el-table-column prop="_id"
                       label="ID"
                       width="220">
      </el-table-column>
      <el-table-column prop="name"
                       label="">
      </el-table-column>
      <el-table-column prop="title"
                       label="">
      </el-table-column>
      <el-table-column prop="avatar"
                       label="">
        <template slot-scope="scope">
          <img :src="scope.row.avatar"
               alt=""
               style="height:3rem">
        </template>
      </el-table-column>
      <el-table-column fixed="right"
                       label=""
                       width="180">
        <template slot-scope="scope">
          <el-button type="text"
                     @click="$router.push(`/heroes/edit/${scope.row._id}`)"
                     size="small"></el-button>
          <el-button type="text"
                     @click="remove(scope.row)"
                     size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  data () {
    return {
      items: []
    }
  },
  methods: {
    async fetch () {
      const res = await this.$http.get('rest/heroes')
      this.items = res.data
    },
    async remove (row) {
      this.$confirm(` ${row.name}`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const res = await this.$http.delete(`rest/heroes/${row._id}`)
        this.$message({
          type: 'success',
          message: '!'
        });
        this.fetch()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    }
  },
  created () {
    this.fetch()
  },
}
</script>

